@mixin border($color, $size){
    color: $color;
    box-sizing: border-box;
    border-width: $size;
    border-style: solid;
}

@mixin dimensions($color, $width, $height, $radius){
    background: $color;
    width: $width;
    height: $height;
    border-radius: $radius;
}

@mixin fontSettings($color, $size){
    color: $color;
    font-size: $size;
    font-weight: 100;
}
